import React, { useState, useEffect } from "react";
import { v4 as uuidv4 } from "uuid";
import { TodosList } from "./TodosList";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/bootstrap.css";
import CountryTime from "./CountryTime";

export const Form = () => {
  const initialValue = { FirstName: "", LastName: "", Email: "" };
  const [input, setInput] = useState(initialValue.FirstName);
  const [lastName, setLastName] = useState(initialValue.LastName);
  const [email, setEmail] = useState(initialValue.Email);
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [todos, setTodos] = useState([]);
  const [editTodo, setEditTodo] = useState(null);

  const updateTodo = (title, lastName, email, phone, address, id) => {
    const newTodo = todos.map((todo) =>
      todo.id === id ? { title, lastName, email, phone, address, id } : todo
    );
    setTodos(newTodo);
    setEditTodo("");
  };

  useEffect(() => {
    if (editTodo) {
      setInput(editTodo.title);
      setLastName(editTodo.lastName);
      setEmail(editTodo.email);
      setPhone(editTodo.phone);
      setAddress(editTodo.address);
    } else {
      setInput("");
      setLastName("");
      setEmail("");
      setPhone("");
      setAddress("");
    }
  }, [setInput, setLastName, setEmail, setPhone, setAddress, editTodo]);

  const onInputChange = (event) => {
    setInput(event.target.value);
  };

  const LastNameChange = (event) => {
    setLastName(event.target.value);
  };

  const EmailChange = (event) => {
    setEmail(event.target.value);
  };

  const AddressChange = (event) => {
    setAddress(event.target.value);
  };

  const onFormSubmit = (event) => {
    event.preventDefault();
    if (!editTodo) {
      setTodos([
        ...todos,
        {
          id: uuidv4(),
          title: input,
          lastName: lastName,
          email: email,
          phone: phone,
          address: address,
        },
      ]);
      setLastName("");
      setInput("");
      setEmail("");
      setPhone("");
      setAddress("");
    } else {
      updateTodo(input, lastName, email, phone, address, editTodo.id);
    }
  };

  return (
    <div>
      <form onSubmit={onFormSubmit}>
        <label htmlFor="">FirstName:</label>
        <input
          type="text"
          placeholder="FirstName"
          className="task-input"
          value={input}
          required
          minLength="5"
          onChange={onInputChange}
        />
        <label htmlFor="">LastName:</label>
        <input
          type="text"
          placeholder="FirstName"
          className="task-input"
          value={lastName}
          required
          minLength="5"
          onChange={LastNameChange}
        />
        <label htmlFor="">Email:</label>
        <input
          type="email"
          placeholder="Email"
          className="task-input"
          value={email}
          required
          onChange={EmailChange}
        />

        <div>
          <label htmlFor="phoneNumber">Phone Number:</label>
          <PhoneInput
            country={"eg"}
            enableSearch={true}
            className="select"
            value={phone}
            onChange={(phone) => setPhone(phone)}
          />
        </div>
        <br />
        <label htmlFor="">Address:</label>
        <input
          type="address"
          placeholder="Address"
          className="task-input"
          value={address}
          required
          onChange={AddressChange}
        />
        <br />
        <CountryTime />

        <button className="button-add" type="submit">
          {editTodo ? "OK" : "Submit"}
        </button>
      </form>
      <div>
        <TodosList
          todos={todos}
          setTodos={setTodos}
          setEditTodo={setEditTodo}
        />
      </div>
    </div>
  );
};
