import React, { useEffect } from "react";
import { useFormik } from "formik";
import Select from "react-select";
import csc from "country-state-city";

export default function CountryTime() {
  const addressFromik = useFormik({
    initialValues: {
      country: null,
      state: null,
      city: null,
    },
  });

  const countries = csc.getAllCountries();

  const updatedCountries = countries.map((country) => ({
    label: country.name,
    value: country.id,
    ...country,
  }));

  const updatedStates = (countryId) =>
    csc.getStatesOfCountry(countryId).map((state) => ({
      label: state.name,
      value: state.id,
      ...state,
    }));

  const updatedCities = (stateId) =>
    csc.getCitiesOfState(stateId).map((city) => ({
      label: city.name,
      value: city.id,
      ...city,
    }));

  const { values, setFieldValue, setValues } = addressFromik;

  useEffect(() => {}, [values]);

  return (
    <div>
      <div>
        {/* <form onSubmit={handleSubmit}> */}
        <Select
          id="country"
          className="select-list"
          name="country"
          label="country"
          options={updatedCountries}
          value={values.country}
          onChange={(value) => {
            setValues({ country: value, state: null, city: null }, false);
          }}
        />
        <Select
          id="state"
          className="select-list"
          name="state"
          options={updatedStates(values.country ? values.country.value : null)}
          value={values.state}
          onChange={(value) => {
            setValues({ state: value, city: null }, false);
          }}
        />
        <Select
          id="city"
          className="select-list"
          name="city"
          options={updatedCities(values.state ? values.state.value : null)}
          value={values.city}
          onChange={(value) => setFieldValue("city", value)}
        />
      </div>

      {/* </form> */}
      <div style={{ padding: "10px", color: "white", textAlign: "center" }}>
        <p>{values.country ? <div>{values.country.label}</div> : null}</p>
        <p>{values.state ? <div>{values.state.label}</div> : null}</p>
        <p>{values.city ? <div>{values.city.label}</div> : null}</p>
      </div>
    </div>
  );
}
